<template>
  <div>
    <van-nav-bar
      :fixed="true"
      title="运维管理"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      
    </van-nav-bar>
    <div class="body">
         <div @click="toDayControl">
             日常运维
         </div>
         <div @click="toEmergencyMaintain">
             应急运维
         </div>
      </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
      toDayControl(){
        this.$router.push("");
      },
      toEmergencyMaintain(){
        this.$router.push("");
      }
  },
  created() {},
  mounted() {},
  destroyed() {}
};
</script>
<style scoped>
.body{
    margin-top: 46px;
    background-color: red;
}
</style>


